html,
body,
#root,
canvas {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  outline: none;
}

body {
  background: black;
  width: 50%;
}

body:before {
  content: 'ЗДЕСЬ ДОЛЖНА БЫТЬ СЦЕНА ЕСЛИ ЕЕ НЕТ ЗВОНИТЕ В ПОЛИЦИЮ';
  color: white;
  position: absolute;
  top: 50%;
  left: 0%;
}

.controls {
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  width: 50%;
  z-index: 100;
  flex-wrap: wrap;
}

.control {
  font-size: 2rem;
  text-decoration: none;
  color: white;
  margin: 0 0.5rem;
}
.control.selected {
  border-bottom: 3px dashed;
}
